import React from 'react'

export default () => (
    <div style={{ textAlign: 'justify' }}>
        <h1 style={{ textAlign: 'center', marginTop: 0 }}>
            Welcome to Space Exploration!
        </h1>
        <p>This is a very simple and fun game to play.</p>
        <p>You are a space explorer looking for interplanetary treasures and you have just reached a new planet. You need to land your spaceship as close as possible to the treasure. But you only get to know the treasure’s exact location after landing! So the only tool you have is your own intuition.</p>
        <p style={{textDecoration:"underline"}}>You can freely rotate the planet and and even zoom in or out. <b>To rotate the planet, click, hold and drag. To set your landing spot, just double click.</b> The more accurate you are, the better. We strongly recommend that you use a mouse for a better experience with the game.</p>
        <p>As soon as you define your landing spot, the exact location of the treasure is revealed, along with a score. The closer to the treasure you've landed, the more accurate was your attempt and so the higher is your score. Maximum score is 100 pts.</p>
        <p>Each game session is composed of 20 attempts, but you can do as many sessions as you wish. The more you play, the more you can practice your intuition!</p>
        <p>By the end of each session, you will find your score. The score of each of your 20 attempts is shown. The last line of the table represents your final score. It ranges from 0 to 100 pts. The higher the final score is, the closer your clicks were to the target, overall.</p>
        <p>Relax, and let your intuition guide you on each click!</p>
        <p>Start now and have fun!</p>
    </div>
)


