import React from 'react'
import US from './US'
import BR from './BR'

// React Flags
import ReactFlagsSelect from 'react-flags-select';

export default ({ lang, setLang }) => (
    <div style={{ textAlign: "center" }}>
        <ReactFlagsSelect
            countries={["US", "BR"]}
            customLabels={{ "US": "English", "BR": "Português" }}
            defaultCountry="US"
            onSelect={val => setLang(val)}
            showSelectedLabel={false}
        />
        {lang === 'US' ? (<US />) : (<BR />)}
    </div>
)


