import React, {useState} from 'react'
import Task from './Task.component'
import { useMutation } from '@apollo/react-hooks'
import { ADD_ANSWER } from '../../schemas/answer'

const getRandomArbitrary = (min, max) => Math.random() * (max - min) + min;
let randomPos = [0, 0]
let answers = []

const updateRandom = () => {
    randomPos = [getRandomArbitrary(-90, 90), getRandomArbitrary(-180, 180)]
}
updateRandom()

export default ({ntrials}) => {
    const debug = window.qparsed.debug === "1"
    const limit = debug ? 4 : 20

    const [waiting, setWaiting] = useState(false)

    const [addAnswer] = useMutation(ADD_ANSWER, {
        onCompleted({ newAnswer: { answer: {answer, random, score}} }) {
            answers.push({
                answer: JSON.parse(answer),
                random: JSON.parse(random),
                score: score
            })
            localStorage.setItem('answers', JSON.stringify(answers))
            setWaiting(true)
        },
        ignoreResults: false
    })
    
    console.log("Posicao aleatoria:" + randomPos)

    const click = async (lat, long) => {
        addAnswer({
            variables: {
                uuid: localStorage.getItem('session'),
                latitude: lat,
                longitude: long,
                latitudeRandom: randomPos[0],
                longitudeRandom: randomPos[1]
            }
        })
    }

    const lastScore = () => {
        if (answers.length === 0) {
            return 0
        }

        const last = answers[answers.length - 1]
        return last.score
    }

    const handleNext = () => {
        updateRandom()
        setWaiting(false)
    }

    // const changeTarget = (settings) => {
    //     window.openMap(settings, {
    //         initial: [0,0],
    //         onDoubleClick: click,
    //         onMove: (lat, long) => setLastMove([lat, long])
    //     })
    // }

    const progress = (answers.length / limit) * 100
    return (
        <Task
            progress={progress}
            waiting={waiting}
            randomPos={randomPos}
            click={click}
            handleNext={handleNext}
            score={lastScore()}
        />
    )
}


