// Register.jsx
import React, { useState } from 'react';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { Grid } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Intro from './Intro';
import Form from './Form';

const classes = theme => ({
    spaceTop: {
        marginTop: theme.spacing.unit * 2,
    },
    root: {
        justifyContent: 'center'
    }
});

localStorage.removeItem('lang')

const RegisterFormComponent = ({ handleSubmit }) => {
    const [lang, setLang] = useState('US')

    localStorage.setItem('lang', lang)

    return (
        <React.Fragment>
            <Dialog
                open={true}
                aria-labelledby="form-dialog-title"
            >
                <DialogContent>
                    
                    <Intro lang={lang} setLang={setLang}/>
                    <Grid container spacing={24}>
                        <Grid item xs={12}>
                            <Form lang={lang} handleSubmit={handleSubmit} />
                        </Grid>
                    </Grid>

                    <DialogActions classes={{ root: classes.root }}>
                    </DialogActions>
                </DialogContent>
            </Dialog>
        </React.Fragment>
    )
}
export default withStyles(classes)(RegisterFormComponent);


