import React from 'react'

import { Formik } from 'formik'

import Button from '@material-ui/core/Button';
import { FieldText } from '../../../../../components/Form'
import { FormStyle, LiActions } from './RegisterFormStyle'
import RegisterFormSchema, { initialValues } from './RegisterForm.schema'

export default ({ lang, handleSubmit }) => (
    <Formik
        initialValues={initialValues}
        validationSchema={RegisterFormSchema}
        onSubmit={data => handleSubmit(data)}
    >
        {props => (
            <FormStyle>
                <FieldText
                    {...props}
                    fieldName="email"
                    title='E-mail:'
                />
                <LiActions>
                    <div style={{ textAlign: 'center', marginTop: 20, marginBottom: 40 }}>
                        <Button variant="outlined" type="submit" color="default">
                            {lang === 'BR' ? 'Começar' : 'Start'}
                        </Button>
                    </div>
                </LiActions>
            </FormStyle>
        )}
    </Formik>
)


