import React from 'react'

export default () => (
    <div style={{ textAlign: 'justify' }}>
        <h1 style={{ textAlign: 'center', marginTop: 0 }}>
            Bem-vindo à Exploração Espacial! 
        </h1>
        <p>Este é um jogo muito simples e divertido de jogar. Nele, você é um explorador espacial que procura tesouros interplanetários e acaba de chegar a um novo planeta. Você precisa pousar sua nave o mais próximo possível do tesouro. Mas você só conhece a localização exata do tesouro após o pouso! Portanto, a única ferramenta que você tem é a sua própria intuição.</p>
        <p style={{ textDecoration: "underline"}}>Você pode girar livremente o planeta e até dar zoom. <b>Para girar o planeta, clique, segure e arraste.</b> Para definir seu local de pouso, clique duas vezes. Quanto mais preciso você for, melhor. É altamente recomendável que você use o mouse para uma melhor experiência com o jogo.</p>
        <p>Assim que você define seu local de pouso, a localização exata do tesouro é revelada, juntamente com uma pontuação. Quanto mais próximo do tesouro você pousar, mais precisa será sua tentativa e, portanto, maior será sua pontuação. A pontuação máxima é de 100 pontos.</p>
        <p>Cada sessão do jogo é composta de 20 tentativas, mas você pode fazer quantas sessões desejar. Quanto mais você joga, mais você pratica a sua intuição!</p>
        <p>No final de cada sessão, você encontrará uma tabela de pontuação. A pontuação de cada uma de suas 20 tentativas é apresentada. A última linha da tabela representa sua pontuação final. Ela varia de 0 a 100 pts. Quanto maior a pontuação final, mais próximos do tesouro foram seus pousos, de uma maneira geral.</p>
        <p>Relaxe e deixe sua intuição guiá-lo a cada clique!</p>
        <p>Comece agora e divirta-se!</p>
    </div>
)


