// Cadastro.jsx
import React from 'react';
import { Typography } from '@material-ui/core';

const mean = numbers => numbers.reduce((acc, val) => (val.score) + acc, 0) / numbers.length;
const lang = localStorage.getItem('lang')

export default ({ data, pvalue }) => (
    <div id="end">
        <Typography component="h1" variant="h4" align="center">
            {lang === 'US' ? 'Game finished!' : 'Jogo concluído!'}
        </Typography>
        <table border='1'>
            <tbody>
                <tr>
                    <th>#</th>
                    <th>{lang === 'US' ? 'Score' : 'Pontuação'}</th>
                </tr>
                {data.map((e, i) => (
                    <tr key={i}>
                        <td>{i + 1}</td>
                        <td>{(e.score).toFixed(2)} pts</td>
                    </tr>
                ))}
                {<tr>
                    <td>{lang === 'US' ? 'Mean' : 'Média'}</td>
                    <td>{mean(data).toFixed(2)} pts</td>
                </tr>}
            </tbody>
        </table>
    </div>
)



