import { gql } from 'apollo-boost';

/** QUERIES */
const SINGLE_ANSWER = gql`
query FilterAnswer($id: ID!){
  node(id: $id) {
    id
    ... on SessionType {
      uuid
      answers {
        edges {
          node {
            id
            answer
            random
            date
            score
          }
        }
      }
    }
  }
}
`

/** MUTATIONS */
const ADD_ANSWER = gql`
mutation ADDAnswer ($uuid: String!, $latitude: Float!, $longitude: Float!, $latitudeRandom: Float!, $longitudeRandom: Float!) {
    newAnswer (uuid: $uuid, data: {
        latitude: $latitude, 
        longitude: $longitude, 
        latitudeRandom: $latitudeRandom, 
        longitudeRandom: $longitudeRandom
    }) {
        answer {
            answer
            random
            score
            sessionId
        }
    }
}
`

export {
    SINGLE_ANSWER,
    ADD_ANSWER
};


